.modal-container {
    text-align: right;
}
.button-close {
    margin-bottom: 15px;
    cursor: pointer;
    font-size: 0;
    position: relative;
    width: 20px;
    height: 20px;
}
.button-close::before {
    content: '';
    position: absolute;
    width: 10px;
    height: 1px;
    top: 7px;
    left: 3px;
    background: #000;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.button-close::after {
    content: '';
    position: absolute;
    width: 10px;
    height: 1px;
    top: 7px;
    left: 3px;
    background: #000;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: table;
    -webkit-transition: opacity 0.3s ease;
    transition: opacity 0.3s ease;
    display: none;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.modal-default-button {
    width: 100%;
    background:  #00ec9b;
    color: #fff;
    padding: 10px;
    border: none;
    cursor: pointer;
    font-weight: bold;
}
.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}
.modal-container {
    width: 300px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
            box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    font-family: Helvetica, Arial, sans-serif;
}
.modal-header h3 {
    margin-top: 0;
    color: #42b983;
}
.modal-body {
    margin: 20px 0;
}
.modal-enter {
    opacity: 0;
}
.modal-leave-active {
    opacity: 0;
}
.modal-enter .modal-container,
.modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
.form-textarea {
    resize: vertical;
}
